<template>
    <div v-if="showTicketLoading" class="d-flex justify-content-center">
        <div class=" spinner-border spinner-border-sm"></div>
    </div>
    <section v-else id="select-ticket">
        <div class="container">
            <div class="concert-banner">
                <div class="d-grid h-100">
                    <div class="concert-banner__caption p-5">
                        <h1 class="concert-banner__caption--title pe-2 mb-2">
                            {{ showTicketsData.program.artist.name + ' ' + showTicketsData.program.artist.family }}</h1>
                        <p class="concert-banner__caption--info d-flex justify-content-start align-items-center mb-2">
                            <img src="../../assets/images/ticket-location.svg" alt="" class="me-1" width="25.1px"
                                height="25.1px">
                            {{ showTicketsData.program.hall.city.city_name }} ،{{ showTicketsData.program.hall.hall_name
                            }}
                        </p>
                        <p class="concert-banner__caption--info d-flex justify-content-start align-items-center mb-2">
                            <img src="../../assets/images/ticket-calendar.svg" alt="" class="me-1" width="25.1px"
                                height="25.1px">
                            {{ showTicketsData.program.run_date.split(' ')[0] }}
                        </p>
                    </div>
                    <div class="concert-banner__image">
                        <img class="h-100" :src="adminUrl + showTicketsData.program.cover"
                            :alt="showTicketsData.program.hall.hall_name">
                    </div>
                </div>
            </div>
            <div class="select-ticket mb-5 mt-4">
                <h2 class="select-ticket__heading d-flex justify-content-start align-items-center mb-2">
                    <img src="../../assets/images/ticket2.svg" alt="" class="me-2" width="24px" height="26px">
                    بلیت خریداری شده
                </h2>
                <ul class="select-ticket__list my-5">
                    <li v-for="(ticket, tk) in showTicketsData.tickets" :key="tk"
                        class="select-ticket__item w-100 d-flex justify-content-between align-items-center px-0">
                        <div class="select-ticket__item--receipt-icon d-md-block d-none">
                            <img src="../../assets/images/receipt2.svg" alt="" class="select-ticket__item--receipt-img"
                                width="24px" height="24px">
                        </div>
                        <div class="select-ticket__paper row mx-3">
                            <div class="col-lg-4 col-md-6 py-3 position-relative">
                                <div class="select-ticket__column-one pb-md-0 pb-3 pe-2">
                                    <div class="d-flex align-items-center">
                                        <div class="select-ticket__number d-flex justify-content-between">
                                            <p class="p-0">تعداد بلیط:</p>
                                            <span class="p-0">{{ showTicketsData.order.tickets_count }}</span>
                                            <span class="p-0">{{ tk + 1 }}</span>
                                        </div>
                                        <img :src="ticket.QRCODE" alt="" class="select-ticket--qr col"
                                            style="max-width: 120px; max-height: 120px;margin: 0 22px 17px 10px;">
                                        <div class="select-ticket__customer-info col ps-1">
                                            <div class="select-ticket__customer-info--item d-flex flex-wrap">
                                                <span class="select-ticket__customer-info--span pe-1">نام خریدار:</span>
                                                <span class="select-ticket__customer-info--span-bold">
                                                    {{ user.name + ' ' + user.family }}</span>
                                            </div>
                                            <div class="select-ticket__customer-info--item d-flex flex-wrap mb-3">
                                                <span class="select-ticket__customer-info--span pe-1">تلفن همراه:</span>
                                                <span class="select-ticket__customer-info--span">
                                                    {{ user.mobile }}
                                                </span>
                                            </div>
                                            <div class="select-ticket__customer-info--item d-flex flex-wrap">
                                                <span class="select-ticket__customer-info--span pe-1">
                                                    شماره فاکتور:
                                                </span>
                                                <span class="select-ticket__customer-info--span">
                                                    {{ showTicketsData.factor.number }}
                                                </span>
                                            </div>
                                            <div class="select-ticket__customer-info--item d-flex flex-wrap">
                                                <span class="select-ticket__customer-info--span-bold pe-1">
                                                    شماره بلیط:
                                                </span>
                                                <span class="select-ticket__customer-info--span-bold">
                                                    {{ ticket.number_ticket }}
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="select-ticket__seating-position d-flex justify-content-around ms-3">
                                        <div class="d-flex">
                                            <span class="select-ticket__seating-position--text">ردیف</span>
                                            <span class="select-ticket__seating-position--number">
                                                {{ ticket.number_row }}
                                            </span>
                                        </div>
                                        <div class="d-flex">
                                            <span class="select-ticket__seating-position--text">صندلی</span>
                                            <span class="select-ticket__seating-position--number">
                                                {{ ticket.number_chair }}
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-lg-4 col-md-6 d-flex align-items-center py-3 ps-md-0 ps-3">
                                <div class="select-ticket__concert-info w-100">
                                    <p class="select-ticket__concert-info--name mb-1">
                                        {{ showTicketsData.program.title }}
                                    </p>
                                    <p
                                        class="select-ticket__concert-info--item d-flex justify-content-start align-items-center mb-1">
                                        <img src="../../assets/images/Location-black.svg"
                                            :alt="showTicketsData.program.title" class="me-1" width="9.17px"
                                            height="11px">
                                        {{ showTicketsData.program.hall.city.city_name }}
                                        ،{{ showTicketsData.program.hall.hall_name }}
                                    </p>
                                    <p
                                        class="select-ticket__concert-info--item d-flex justify-content-start align-items-center mb-1">
                                        <img src="../../assets/images/Calendar-black.svg" alt="" class="me-1"
                                            width="11.3px" height="12.2px">
                                        {{ showTicketsData.program.run_date.split(' ')[0] }}
                                    </p>
                                    <p
                                        class="select-ticket__concert-info--item d-flex justify-content-start align-items-center">
                                        <img src="../../assets/images/Clock-black.svg" alt="" class="me-1"
                                            width="11.7px" height="11.7px">
                                        {{ showTicketsData.san.start_date.split(' ')[1] }}
                                    </p>
                                    <div
                                        class="select-ticket__concert-info--total d-flex flex-wrap justify-content-between align-items-center pt-4">
                                        <p class="select-ticket__concert-info--total-text text-start">
                                            مبلغ کل با احتساب ۹٪ ارزش افزوده:
                                        </p>
                                        <p class="select-ticket__concert-info--total-price text-end">
                                            {{ showTicketsData.factor.price.toLocaleString() }} تومان
                                        </p>
                                    </div>
                                </div>
                            </div>
                            <div class="col-lg-4 d-lg-block d-none p-0 select-ticket__column-three">
                                <div class="select-ticket__performer">
                                    <img :src="adminUrl + showTicketsData.program.cover"
                                        :alt="showTicketsData.program.title" class="select-ticket__performer--img">
                                    <span class="select-ticket__performer-overlay"></span>
                                </div>
                            </div>
                        </div>
                    </li>
                </ul>
                <div class="select-ticket__more ms-auto px-3">
                    <div class="d-flex flex-wrap justify-content-sm-between justify-content-center">
                        <ul class="nav justify-content-start align-items-end">
                            <li class="nav-item me-3 mb-2">
                                <a @click.prevent="download()" id="select-ticket__more--download" href="#"
                                    class="select-ticket__more--download nav-link d-flex justify-content-between align-items-center">
                                    <div v-if="downloadLoading" class="d-flex justify-content-center">
                                        <div class="spinner-border spinner-border-sm"></div>
                                    </div>
                                    <template v-else>
                                        <img src="../../assets/images/document-download.svg" alt="" class="me-2"
                                            width="20px" height="20px">
                                        دانلود pdf
                                    </template>
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script setup>
import { ref, reactive, computed } from "vue";
import { useRoute } from "vue-router";
import { useUserStore } from '../../store/user'
import usersApi from '@/axios/api'
import { useToast } from "vue-toastification";
import { useHead } from '@vueuse/head'
import { handleError } from "@/utils/helpers";

const userStore = useUserStore()
const toast = useToast();
const user = reactive(userStore.getUser)
const route = useRoute()
const adminUrl = process.env.VUE_APP_BASE_URL

let showTicketLoading = ref(false)
let downloadLoading = ref(false)
let showTicketsData = reactive({ program: {}, san: {}, factor: {}, order: {} })

indexs();

async function indexs() {
    showTicketLoading.value = true
    try {
        const apiUrl = `user/panel/programs/${route.params.program_id}/sans/${route.params.san_id}/orders/${route.params.order_id}/show/tickets`;
        const response = await usersApi.post(apiUrl)
        if (response.data.status == 'success') {
            showTicketsData = response.data.data;
            useHead({
                // Can be static or computed
                title: computed(() => `${userStore.getUser.name} ${userStore.getUser.family}- ${showTicketsData.program.title}`),
            })
            showTicketLoading.value = false
        }
    } catch (err) {
        toast.error(handleError(err).join(', '), {
            timeout: 2000
        });
    } finally {
    }
}
async function download() {
    downloadLoading.value = true;
    try {
        const apiUrl = `user/panel/programs/${route.params.program_id}/sans/${route.params.san_id}/orders/${route.params.order_id}/download/pdf`;
        const response = await usersApi.get(apiUrl, {
            responseType: "blob",
        });
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", 'tickets.pdf');
        document.body.appendChild(link);
        link.click();
        link.remove();

        downloadLoading.value = false
    } catch (err) {
        toast.error(handleError(err).join(', '), {
            timeout: 2000
        });
    } finally {
    }
}
</script>